import React from 'react'
import "./css/ReactInfo.css";


import AOS from 'aos';
import 'aos/dist/aos.css';

function ReactInfo() {

    AOS.init();

    return (
        <section id="reactInfo">
        <div className="reactInfo" data-aos="fade-up">
            <div className="container">
                <div className="rInfo__info" >
                <h3>
                Development using <span>ReactJS</span> Framerwork. ReactJS is superfast framework for websites which is used by many internet giants... <span>Facebook, Instagram, Netflix, and many more</span>
                </h3>
                </div>
        
                <hr className="solid" />

            </div>

        </div>
        </section>
    )
}

export default ReactInfo
