import React from 'react';
import "./css/Body.css";
import downArrow from "./images/down-arrow.png";
import webImage from "./images/www (1).png";

import {useSpring, animated} from 'react-spring';





function Body() {

 const propsBody = useSpring({opacity: 1, from :{opacity:0}},{config:{duration:1000}});
  
const propsLeft = useSpring({opacity: 1,marginLeft:0, from: {opacity: 0,marginLeft: -500},delay:1000},{config:{duration:1000} });

const propsRight = useSpring({opacity: 1,marginRight:0, from: {opacity: 0,marginRight: -500},delay:1000},{config:{duration:1000} });


    return (
      
<animated.div style={propsBody}>
      <section id="home__body">
        <div className="body">
            <div className="container">
            <div className="row body__inner">


              <div className="col-md-6 body__left">
              <animated.div style={propsLeft}>
                <p className="body__text"><span className="big__letter">L</span>et the World Discover You
                                        <br /><span className="big__lettera">G</span>row Your Bussiness Online<br />
                                        <span className="big__lettera">T</span>ime to make a website</p>
                
                {/* Beautiful, Professional and Mobile Responsive Website Designs */}
              </animated.div>

              </div>


             <div className="col-md-6 body__right">
             <animated.div style={propsRight}>

                <img src={webImage} alt="modern developer" className="body__rightImage" />
              </animated.div>

              </div>
       
                           

            </div>

            <div className="row body__cat">

        

                <div className="col-sm-6">
              <animated.div style={propsLeft}>

                  <h2 className="cat__neuro">Develop Your Own Website Now</h2>
              </animated.div>


                </div>


                <div className="col-sm-6 ">
                <a href="#contact" className="cat__link">
             <animated.div style={propsRight}>

                <button type="button" class="btn btn-warning body__button" >Get In Touch
                <img src={downArrow} alt="" className="body__arrow" />
                
                </button>
              </animated.div>
                
                </a>
                

                </div>
      

            </div>
        

            </div>
            <div className="bottom">
            
            </div>
         </div>
         </section>
         </animated.div>

    )
}

export default Body


