import React from 'react';
import "../css/Need.css";


import AOS from 'aos';
import 'aos/dist/aos.css';


import { makeStyles } from '@material-ui/core/styles';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineOppositeContent from '@material-ui/lab/TimelineOppositeContent';
import TimelineDot from '@material-ui/lab/TimelineDot';

import BusinessIcon from '@material-ui/icons/Business';
import PhotoLibraryOutlinedIcon from '@material-ui/icons/PhotoLibraryOutlined';
import ShoppingBasketOutlinedIcon from '@material-ui/icons/ShoppingBasketOutlined';
import ContactMailOutlinedIcon from '@material-ui/icons/ContactMailOutlined';


import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';


const useStyles = makeStyles((theme) => ({
    paper: {
      padding: '6px 16px',
    },
    secondaryTail: {
      backgroundColor: theme.palette.secondary.main,
    },
  }));
  

  
 

function Need() {

    const classes = useStyles();

    AOS.init();


    return (
        <div className="need">
            <div className="container">
            <h2 className="need__heading" data-aos="zoom-in-up">Why Do You Need A Website in 2020?</h2>

            <Timeline align="alternate">
        <TimelineItem align="left">
        <TimelineOppositeContent>
            <Typography data-aos="fade-right">Website makes you stand out and attract more customers</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator data-aos="fade-up">
            <TimelineDot color="primary">
              <BusinessIcon />
            </TimelineDot>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Paper elevation={3} className={classes.paper} data-aos="fade-left">
              <Typography variant="h6" component="h1" data-aos="fade-left">
                Business
              </Typography>
              <Typography data-aos="fade-left">Having a website definitely sounds cooler</Typography>
            </Paper>
          </TimelineContent>
          
        </TimelineItem>
        
        <TimelineItem>
          <TimelineOppositeContent>
            <Typography data-aos="fade-left">Show all types of your great works -- maybe your garden, projects or your favourite collection</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator data-aos="fade-up">
            <TimelineDot color="secondary">
              <PhotoLibraryOutlinedIcon />
            </TimelineDot>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent>
            <Paper elevation={3} className={classes.paper} data-aos="fade-right">
              <Typography variant="h6" component="h1">
                You Love To Draw
              </Typography>
              <Typography>Showcasing all your drawing on a website..</Typography>
            </Paper>
          </TimelineContent>
        </TimelineItem>


        <TimelineItem>
        <TimelineOppositeContent>
            <Typography data-aos="fade-right">Display all your products at one place, show testimonials, product reviews, etc</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator data-aos="fade-up">
            <TimelineDot color="primary" variant="outlined">
              <ShoppingBasketOutlinedIcon />
            </TimelineDot>
            <TimelineConnector className={classes.secondaryTail} />
          </TimelineSeparator>
          <TimelineContent>
            <Paper elevation={3} className={classes.paper} data-aos="fade-left">
              <Typography variant="h6" component="h1">
                Want to sell online
              </Typography>
              <Typography>What's more better than an E-commerce site</Typography>
            </Paper>
          </TimelineContent>
        </TimelineItem>


        <TimelineItem>
        <TimelineOppositeContent>
            <Typography data-aos="fade-left">This can include all of your project works and activities</Typography>
          </TimelineOppositeContent>
          <TimelineSeparator data-aos="fade-up">
            <TimelineDot color="secondary">
            <ContactMailOutlinedIcon />
            </TimelineDot>
          </TimelineSeparator>
          <TimelineContent>
            <Paper elevation={3} className={classes.paper} data-aos="fade-right">
              <Typography variant="h6" component="h1">
                Portfolio
              </Typography>
              <Typography>Your Personal Portfolio website will make your resume glow</Typography>
            </Paper>
          </TimelineContent>
        </TimelineItem>
      </Timeline>


      <hr className="solid" />


      <div className="need__learn">
        <h1 data-aos="fade-up">Want to Learn Web Development</h1>
        <h2 data-aos="fade-up">I can Teach you...</h2>
        <a href="https://forms.gle/hyMxLfShK7LPusv86"><p data-aos="fade-up">Learn More</p></a>
      </div>


            </div>
      <hr className="solid" />

        </div>
    )
}

export default Need
