import React from 'react';
import profile from "../images/profile.png";
import "../css/About.css";

import AOS from 'aos';
import 'aos/dist/aos.css';

function About() {

    AOS.init();

    return (
        <section id="about">
        <div className="about">

            <div className="container" data-aos="flip-up">
            <div className="about__neuro">
            <div className="about__imgDiv">
                <h3 className="about__title">About</h3>
                <img src={profile} alt="" className="about__img" data-aos="zoom-in-up"/>
            </div>
            <div className="about__text" data-aos="zoom-in-up">
            <p> Hii Everyone, first of all thanks for coming  in here. I'm Satyam Dahale and if you like high-quality  at low-price then you are visiting the right site :) .I can design any kind of website you need personal, business, blog, e-commerce,etc. Hope to hear from you soon!
            </p>
            </div>
            </div>

            </div>
            <div className="bottom" />
        </div>
        </section>
    )
}

export default About
