import React from 'react'
import appImage from "./images/application.png";
import responsive from "./responsive.png";

import AOS from 'aos';
import 'aos/dist/aos.css';

import "./css/Services.css";

function Services() {
    AOS.init({duration: 800});

    return (
        <section id="services">

        
        <div className="services">
            <div className="container">
                <h2 className="services__title"><span>Services</span></h2>
            
            
            <div className="row serv__data"  data-aos="fade-up">
                <div className="col-sm-4 a">
                    <img src={responsive} alt="" className="serv__img1" />
                </div>
                <div className="col-sm-8 b" >
                   <p className="serv__text1">
                   Website Design
                   
                     </p> 
                     <p className="serv__description">
                        I will design website according to your requirement --- Fancy, Professional, Modern, Classic. Beautiful websites that will get your task done. I can also desing a logo for you. Timely delivery with source code and I will also give regular updates on the progress of the website. I can assure you the you are coming to the right developer.
                    </p>

                </div>
            </div>

            <div className="row serv__data"  data-aos="fadeInUp">
                <div className="col-sm-8 c">
                   <p className="serv__text2">
                    WebApp Design
                    </p>

                    <p className="serv__description">
                     Rather than staic websites we can have all the functionality that an android/ios app has on the web app. Web app is a website with a powerUp which allows it to work as an application. Basically Facebook is a web app. And I can design it according to your requirement.
                    </p>
                </div>
                <div className="col-sm-4 ">
<img src={appImage} alt="" className="serv__img2" />
                 
                </div>
            </div>
            
            </div>

            {/* <div className="bottom"></div> */}
            

        </div>
        </section>
    )
}



export default Services
